.additional {
  @include list-reset;
}

.additional__item {
  padding-top: 13px;
  padding-bottom: 17px;

  border-bottom: 1px solid $border-grey;
}

.additional__title {
  margin: 0;
  margin-bottom: 13px;
  padding: 0;

  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.additional__description {
  display: flex;
  justify-content: space-between;

  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
}

.additional__text {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
}

.additional-products__promo {
  position: relative;
  padding-top: 39px;
  padding-bottom: 32px;
}

.additional-products__icon {
  position: absolute;
  top: 103px;
  left: 113px;

  fill: $white;
}

.additional-products__text {
  position: absolute;
  left: 56px;
  bottom: 62px;

  width: 165px;

  color: $white;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .additional__item {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: -1px;
    padding-top: 26px;
    padding-left: 38px;
    padding-right: 36px;
    padding-bottom: 27px;

    border: 1px solid rgba(205, 205, 205, 0.5);
  }

  .additional__title {
    margin-bottom: 25px;

    font-size: 20px;
    line-height: 20px;
  }

  .additional__description {
    justify-content: flex-end;
    padding-top: 2px;
    width: 290px;

    font-size: 16px;
    line-height: 20px;
  }

  .additional__text--price {
    width: 95px;

    text-align: right;
  }

  .additional-products__promo {
    padding-top: 69px;
    padding-bottom: 73px;
  }

  .additional-products__icon {
    top: 134px;
    left: 91px;
  }

  .additional-products__text {
    top: 142px;
    left: 424px;

    margin: 0;

    text-align: left;
  }
}

@media (min-width: $desktop-width) {
  .additional__item {
    align-items: center;
    flex-wrap: nowrap;
    padding: 15px 0 16px 0;

    border: none;
    border-top: 1px solid rgba(205, 205, 205, 0.5);
    border-bottom: 1px solid rgba(205, 205, 205, 0.5);
  }

  .additional__description {
    justify-content: space-between;
    width: 325px;
  }

  .additional__title,
  .additional__text {
    margin-bottom: 0;
  }

  .additional__title {
    width: 325px;
  }

  .additional__text {
    text-align: left;
  }

  .additional__text--price {
    width: 80px;
  }

  .additional__link {
    width: 245px;
  }

  .additional-products__promo {
    padding: 0;
  }

  .additional-products__icon {
    top: 65px;
    left: 96px;
  }

  .additional-products__text {
    top: 185px;
    left: 40px;

    text-align: center;
  }
}
